  font-weight: 400;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff2") format("woff2"), url("../fonts/Inter-Medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Inter", "Arial", sans-serif;
  color: #344054;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

input {
  transition: outline-offset 0.3s ease; }

:focus:not(:focus-visible) {
  outline: none; }

:focus-visible {
  outline-color: #6941C6; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh)); }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
  display: grid;
  place-items: center; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 8px 20px;
  font: inherit;
  font-weight: 600;
  letter-spacing: inherit;
  color: #ffffff;
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
  background-color: #7F56D9;
  border: 1px solid #7F56D9;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, outline-offset 0.3s ease;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none; }
  .btn:disabled {
    background-color: #E9D7FE;
    border-color: #E9D7FE;
    pointer-events: none;
    cursor: not-allowed; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn:hover, .btn:focus {
      background-color: #6941C6;
      border: 1px solid #6941C6; } }
  .btn--light {
    color: #344054;
    background-color: #ffffff;
    border-color: #D0D5DD; }
    @media (hover: hover), screen and (min-width: 0\0) {
      .btn--light:hover, .btn--light:focus {
        background-color: #F9FAFB;
        border-color: #D0D5DD; } }

:where(button):where(:not(:active)):focus-visible {
  outline-offset: 4px; }

:where(button) > :where(svg) {
  width: 24px;
  height: 24px;
  flex-shrink: 0; }

.link {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.4;
  color: #6941C6;
  text-decoration: none;
  transition: color 0.3s ease, outline-offset 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .link:hover, .link:focus {
      color: #53389E; } }

.login {
  width: 360px;
  display: flex;
  flex-direction: column; }
  @media (max-width: 424px) {
    .login {
      width: 100%; } }

.login__image {
  margin: 0 0 24px;
  align-self: center; }

.login__title {
  margin: 0 0 12px;
  align-self: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 1.3; }
  @media (max-width: 424px) {
    .login__title {
      font-size: 24px; } }

.login__welcome {
  margin: 0 0 32px;
  align-self: center;
  color: #667085; }

.login__form {
  margin: 0 0 32px; }

.login__input {
  margin: 0 0 12px; }
  .login__input label {
    display: block;
    margin: 0 0 4px;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.4; }
  .login__input input {
    width: 100%;
    padding: 12px 16px;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    background-color: #ffffff;
    border: 1px solid #D0D5DD;
    border-radius: 8px;
    transition: background-color 10000000s ease-out, outline-offset 0.3s ease; }
    .login__input input::placeholder {
      font: inherit;
      color: #667085;
      transition: color 0.3s ease; }
    .login__input input:disabled {
      background-color: #F9FAFB; }
    .login__input input:hover::placeholder, .login__input input:focus::placeholder {
      color: #101828; }
    .login__input input:focus {
      outline-offset: 4px; }
  .login__input.is-invalid input {
    border-color: #F04438; }
  .login__input.is-invalid .login__input-error {
    visibility: visible;
    opacity: 1; }

.login__input-error {
  font-size: 14px;
  line-height: 1.4;
  color: #F04438;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease; }

.login__checkbox {
  margin: 0 0 24px;
  display: flex;
  align-items: center; }
  .login__checkbox input {
    position: relative;
    margin: 0 4px 0 0;
    appearance: none;
    display: grid;
    place-items: center;
    width: 16px;
    height: 16px;
    background-color: #ffffff;
    border: 1px solid #D0D5DD;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease, border-color 0.3s ease, outline-offset 0.3s ease; }
    .login__checkbox input::after {
      position: absolute;
      content: '✓';
      top: 50%;
      left: 50%;
      font-weight: 600;
      font-size: 16px;
      line-height: 12px;
      color: transparent;
      transform: translate(-50%, -50%);
      transition: color 0.3s ease; }
    .login__checkbox input:checked {
      background-color: #F4EBFF;
      border-color: #7F56D9; }
      .login__checkbox input:checked::after {
        color: #7F56D9; }
    .login__checkbox input:disabled {
      background-color: #F2F4F7;
      pointer-events: none; }
  .login__checkbox label {
    margin: 0 12px 0 0;
    flex-shrink: 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.4; }

.login__forgot {
  margin-left: auto; }
  @media (max-width: 374px) {
    .login__forgot {
      text-align: right; } }

.login__btn {
  width: 100%; }

.login__signup {
  margin: 0;
  align-self: center;
  font-size: 14px;
  line-height: 1.4; }
